body{
    margin: 0;
    box-sizing: border-box;
}
::placeholder{
    color: rgb(150, 23, 23);
    text-align: right;
}
input,button{
    cursor: pointer;
    border: none;
    outline: none;
}
button:hover{
    border: 1px solid brown;
    background-color: rgba(255, 99, 71, 0.507);
}
.info{
    margin-top: -22px;
    background-color: rgba(245, 238, 238, 0.767);
    text-align: center;
    padding-bottom: 10px;
}
.info h1{
    background-color: rgb(8, 8, 0);
    color: white;
}
.formula{
    position: absolute;
    top: 45%;
    left: 2%;
    background-color: rgba(182, 177, 176, 0.486);
    text-align: center;
    width: 25%;
    margin-left: 15px;
}
.formula ul{
    text-align: center;
    list-style-type: none;
}
.formula li{
    text-align: left;
}
.hd{
    background-color: black;
    color: white;
    padding: 5px;
    margin-top: -10px;
}
.calci{
    position: absolute;
    top: 50%;
    left: 35%;
    width: 30%;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.109);
}
.calci h3{
    background-color: black;
    color: white;
    margin-top: 0px;
    padding: 5px;
}
.calci input{
    padding: 5px;
    margin: 8px;
}
.calci h4{
    padding: 15px;
    background-color: black;
    color: white;
    margin-bottom: 0px;
}
button{
   padding: 5px;
   margin-right: 20px ;
   margin-top: 15px;
}
footer{
    position: absolute;
    padding: 10px;
    top: 45%;
    left: 70%;
    background-color: rgba(216, 191, 216, 0.479);
}
table,td,th{
    text-align: center;
    border: 1px solid black;
    border-collapse: collapse;
    padding: 10px;
}
@media only screen and (max-width:800px){
    footer{
      top: 115%;
      width: 40%;
      left: 25%;
    }
    .calci{
      top: 59%;
      width: 50%;
      left: 44%;
    }
    .formula{
       top:55%;
       width: 35%;
    }
}
@media only screen and (max-width:550px){
    footer{
      top: 58%;
      width: 40%;
      left: 47%;
    }
    .calci{
      top: 120%;
      width: 80%;
      left: 5%;
      padding-bottom: 60px;
    }
    .formula{
       top:60%;
       width: 35%;
    }
    table , tr ,td{
        padding: 3px;
    }
}
@media only screen and (max-width:480px){
    footer{
      top: 65%;
      width: 43%;
      left: 52%;
      padding-left: 0px;
    }
    .calci{
      top: 125%;
      width: 80%;
      left: 5%;
      padding-bottom: 60px;
    }
    .formula{
       top:62%;
       text-align: left;
       width: 45%;
       left: 0%;
    }
    table , tr ,td{
        padding: 0px;
    }
}